body, html{
  background-color:rgb(73, 75, 121);
  font-family: "Helvetica";
}

.App {
  text-align: center;
}
#App {
  padding-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#typebox{
  width: 50%;
  position: relative;
  font-size: 18px;
  padding-left: 3px;
  padding-top: 3px;
}

p{
  margin: 0;
  padding: 0;
}
h3{
  margin-top: 0;
  padding-top: 0;
  padding-bottom: 0;
  margin-bottom: 0;
}
h4{
  margin-top: 0;
}
#controls{
  padding-top: 25px;
}
#input-box{
  position: absolute;
  color: transparent;
  caret-color: rgb(0, 0, 0);
  border: none;
  resize: none;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  width: fill + 1;
  height: inherit;
  background-color: transparent;
  font-family: Helvetica;
  font-size: 18px;
}
ul{
  margin: 0 auto;
  list-style:none;
}